import { ElMessage } from 'element-plus';
export default {
  name: 'FileDetailDialog',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    file: {
      type: Object,
      default: null
    },
    fileLink: {
      type: String,
      default: ''
    },
    urls: {
      type: Object,
      default: () => ({
        originUrl: '',
        mdUrl: '',
        htmlUrl: '',
        bbUrl: '',
        tgId: '',
        S3Location: '',
        S3CdnFileUrl: ''
      })
    }
  },
  emits: ['update:modelValue', 'download', 'tagManagement', 'block', 'white', 'delete'],
  data() {
    return {
      activeTab: 'originUrl'
    };
  },
  computed: {
    visible: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit('update:modelValue', val);
      }
    },
    dialogWidth() {
      return window.innerWidth < 768 ? '95%' : '900px';
    },
    descColumn() {
      return window.innerWidth < 768 ? 1 : 2;
    },
    isVideo() {
      //  content-type 
      const fileType = this.file?.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('video')) return true;
      // 
      const name = this.file?.name?.toLowerCase() || '';
      return name.endsWith('.mp4') || name.endsWith('.webm') || name.endsWith('.mov') || name.endsWith('.avi');
    },
    isAudio() {
      //  content-type 
      const fileType = this.file?.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('audio')) return true;
      // 
      const name = this.file?.name?.toLowerCase() || '';
      return name.endsWith('.mp3') || name.endsWith('.wav') || name.endsWith('.ogg') || name.endsWith('.flac');
    },
    isImage() {
      //  content-type 
      const fileType = this.file?.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('image')) return true;
      // 
      const name = this.file?.name?.toLowerCase() || '';
      return name.endsWith('.jpg') || name.endsWith('.jpeg') || name.endsWith('.png') || name.endsWith('.gif') || name.endsWith('.webp') || name.endsWith('.svg') || name.endsWith('.bmp') || name.endsWith('.avif') || name.endsWith('.heic') || name.endsWith('.heif');
    },
    uploadTime() {
      if (this.file?.metadata?.TimeStamp) {
        return new Date(this.file.metadata.TimeStamp).toLocaleString();
      }
      return '';
    },
    accessType() {
      const listType = this.file?.metadata?.ListType;
      const label = this.file?.metadata?.Label;
      if (listType === 'White') return '';
      if (listType === 'Block') return '';
      if (label === 'adult') return '';
      return '';
    }
  },
  methods: {
    handleVideoClick(e) {
      const video = e.target;
      if (video.paused) video.play();else video.pause();
    },
    handleTabClick() {},
    handleUrlClick(e) {
      const input = e.target;
      input.select();
      navigator.clipboard.writeText(input.value).then(() => {
        ElMessage.success('');
      });
    },
    openImageLink() {
      if (this.fileLink) {
        //  ?from=admin 
        const cleanUrl = this.fileLink.replace(/\?from=admin$/, '');
        window.open(cleanUrl, '_blank');
      }
    }
  }
};